import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Card, CardActions, CardHeader, CardTitle, CardText} from 'material-ui/Card';
import TextField from 'material-ui/TextField';

class VerProyecto extends Component{
    
    
    constructor(props) {
        super(props)
        this.state = {
            file: '',
            nombreproyecto: '' ,
            descripcion: '' ,
            codigo: '',
            fecha : '' ,
            creador: ''
            
        
        }
        this.handleChange = this.handleChange.bind(this);
        this.getFileName = this.getFileName.bind(this);
        this.actualizarinfo = this.actualizarinfo.bind(this);
    } 
    
    
        handleChange(e){
        this.setState({
            [e.target.name]: e.target.value
            
        });
        }
        
         getFileName(){
        var name = document.getElementById('documentselector');
        this.state.file = name.files.item(0);
         }
    
    
        actualizarinfo(){
        const archivo  = this.state.file ; 
        const nombrearchivo = this.state.file.name ; 
        const creador = this.state.creador ; 
        const descripcion = this.state.descripcion ;
        const nombreproyeto = this.state.nombreproyecto ; 
        const fecha = this.state.fecha ; 
        
        
        /* todas las variables de la pagina */
        }
    
    
    render() {
        return (
            
            <MuiThemeProvider>
                <div className="container mt-5">
                <div className="row justify-content-center">
                    <div className="col-lg-7 col-md-10">
                        <Card>
                        <CardHeader
                              title="Nombre: "
                              
                              className="text-center  pt-3"
                            />
                            <div className="card-body">
                               <label >Descripcion:</label>
                               <br/>
                                <TextField  value={this.state.descripcion} multiLine={true} rows={5} onChange={this.handleChange}/>
                                <br/>
                            <label >Codigo:</label>
                            <TextField  value={this.state.codigo} onChange={this.handleChange}/>
                             <br/>
                             
                            <label >Fecha:</label>
                            <TextField  value={this.state.fecha} onChange={this.handleChange}/>
                            <br/>
                            <label >Subir Archivo .Zip:</label>
                            <input type='file'  id="documentselector"  onChange={this.getFileName}/>
                            <br/>
                            <label >Creador:</label>
                            <TextField  value={this.state.creador} onChange={this.handleChange}/>   
                            <br/>
                            <div className="text-center">
                            <button  className="btn btn-primary mr-3" onClick={ () => this.actualizarinfo ()}>Actualizar Informacion</button>
                            <button  className="btn btn-primary mr-3" >Borrar Proyecto</button>
                            </div>
                            </div>
                        </Card>
                    </div>

                </div>
            </div >
        </MuiThemeProvider>
            );
    }
    
    
    
    
    
    
    
}
export default VerProyecto;


